.signup{
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    background-color: #d7d5d5;
    font-family: 'Lato', sans-serif;

}
.fform{
    display: flex;
    flex-direction: column;
   padding: 5px;
   margin-top: 100px;
   border: 2px solid black;
   border-radius: 50px;
   background-color:#fff;
   margin-bottom: 10px;

}
.fform input{
    margin: 10px;
    height: 8px;
    font-weight: bold;
  
     background-color: white;
     border-radius: 5px;
     border: 1px solid #3C2A21;
    color: #3C2A21;
    font-size: 16px;
   
}



.submit{
    width: 100px;
    justify-content: center;
    margin: auto;
    height: 24px;
    width: 270px;
    border-radius: 100px;
    padding: 5px 20px;
    height: 40px;
    line-height: 30px;
    background-color: #1A120B ;
    color: #E5E5CB;
}
.name input{
    width: 150px;
}
.input{
    display: flex;
    flex-direction: column;
    
    
}
input[type= "email"]{
    padding:5px  13px 5px 31px ;
}
input[type= "text"]{
    padding:5px  13px 5px 31px ;
}
input[type= "password"]{
    padding:5px  13px 5px 31px ;
}


