@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Outfit:wght@200;500&display=swap');
body{

    background-color: #d7d5d5;
    font-family: 'Lato', sans-serif;
}

.sign-in-sign-up {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.sign-in-sign-up h2{
    font-weight: bold;
    color: #1A120B;

    /* font-size: 1rem; */
}


form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
 
    border-radius: 25px;
    background-color:#fff;
    margin-top: 130px;

    /* border-top: 1px solid black;
    border-left: 1px solid black; */
    
}


input[type="email"]
 {
    margin-bottom: 20px;
  
       width: 250px;
    height: 35px;
  padding: 5px  10px 5px 40px;
    font-weight: bold;
     background-color: white;
     border-radius: 5px;
     border: 1px solid #3C2A21;
    color: #3C2A21;
   
    font-size: 16px;
   
}
.input-password{
    margin-bottom: 20px;
    padding: 5px  10px 5px 40px;
    font-size: 16px;
    border: 1px solid #3C2A21;
    border-radius: 5px;
    background-color: white;
    color: #3C2A21;
    font-weight: bold;
    width: 250px;
    height: 35px;
    margin-left: 0px;
}
.icon{
    position: absolute;
    align-items: center;
    justify-content: center;
    display: flex;
    padding-left: 11px;
    padding-top: 14px;
   
}
.iconn{
padding-left: 10px;
}
