button[type="submit"] {
    padding: 10px 20px;
    
    border-radius: 5px;
    cursor: pointer;
    /* transition: 0.4s; */
     background-color: #1A120B ;
      color: #E5E5CB;
    font-weight: bold;
    font-size: 1rem;
    width: 270px;
    border-radius: 100px;
    
}

button[type="submit"]:hover {
   background-color: #E5E5CB;
   color:#1A120B ;
    
  
}

p {
    margin-top: 20px;
    font-size: 16px;
    color: #1A120B;
    /* font-size: 1.4rem; */
}

p span {
    font-weight: bold;
    color: #1A120B;
    left: 0;
    margin-left: 4px;
    /* font-size: 1rem; */
}

